import * as XLSX from 'xlsx';

//  
const FUEL_TYPES = [
  { value: 'diesel', label: ' ' },
  { value: 'gasoline_95', label: ' -95' }
];

//   
interface FuelTransaction {
  key: string;
  type: 'purchase' | 'sale' | 'drain' | 'base_to_bunker' | 'bunker_to_base';
  volume: number;
  price: number;
  totalCost: number;
  date: string;
  timestamp: number;
  fuelType: string;
  supplier?: string;
  customer?: string;
  vessel?: string;
  frozen?: boolean;
  frozenDate?: number;
  paymentMethod?: 'cash' | 'card' | 'transfer' | 'deferred';
  userId?: string;
  userRole?: string;
  notes?: string;
  edited?: boolean;
  editTimestamp?: number;
}

//      
interface FuelTypeData {
  fuelType: string;
  fuelName: string;
  purchased: number;
  sold: number;
  drained: number;
  balance: number;
  purchaseCost: number;
  saleIncome: number;
  profit: number;
}

//  
interface ExportResult {
  success: boolean;
  fileName?: string;
  error?: string;
}

/**
 *      Excel    
 */
export const exportFuelDataToExcel = (
  transactions: FuelTransaction[],
  fuelTypeData: FuelTypeData[],
  totalPurchased: number,
  totalSold: number,
  totalDrained: number,
  totalPurchaseCost: number,
  totalSaleIncome: number,
  averagePurchasePrice: number,
  averageSalePrice: number,
  coefficient: number,
  profitMargin: number
): ExportResult => {
  try {
    //    Excel
    const workbook = XLSX.utils.book_new();
    
    //      
    const transactionData = transactions.map((item, index) => {
      let typeLabel;
      switch(item.type) {
        case 'purchase': typeLabel = ''; break;
        case 'sale': typeLabel = ''; break;
        case 'base_to_bunker': typeLabel = '    '; break;
        case 'bunker_to_base': typeLabel = '    '; break;
        default: typeLabel = item.type;
      }
      
      let paymentMethodLabel = '-';
      if (item.paymentMethod) {
        switch(item.paymentMethod) {
          case 'cash': paymentMethodLabel = ''; break;
          case 'card': paymentMethodLabel = ''; break;
          case 'transfer': paymentMethodLabel = ''; break;
          case 'deferred': paymentMethodLabel = ' '; break;
        }
      }
      
      return {
        '': index + 1,
        ' ': typeLabel,
        ' ': FUEL_TYPES.find(t => t.value === item.fuelType)?.label || item.fuelType,
        ' ()': item.volume,
        ' (/)': item.type === 'drain' ? '-' : item.price,
        ' ()': item.type === 'drain' ? '-' : item.totalCost,
        '': item.date,
        '': item.vessel || '-',
        ' ': item.type === 'sale' ? paymentMethodLabel : '-',
        '/': item.type === 'purchase' ? item.supplier : item.customer,
        '': item.frozen ? '' : '',
        '': item.notes || ''
      };
    });
    
    //     
    const transactionsSheet = XLSX.utils.json_to_sheet(transactionData);
    XLSX.utils.book_append_sheet(workbook, transactionsSheet, '');
    
    //       
    const fuelTypeSummary = fuelTypeData.map(type => ({
      ' ': type.fuelName,
      ' ()': type.purchased,
      ' ()': type.sold,
      ' ()': type.drained,
      ' ()': type.balance,
      '   ()': type.purchaseCost,
      '   ()': type.saleIncome,
      ' ()': type.profit
    }));
    
    //        
    const fuelTypeSheet = XLSX.utils.json_to_sheet(fuelTypeSummary);
    XLSX.utils.book_append_sheet(workbook, fuelTypeSheet, '  ');
    
    //      
    const summaryData = [
      { '': '  ()', '': totalPurchased },
      { '': '  ()', '': totalSold },
      { '': '  ()', '': totalPurchased - totalSold },
      { '': '   ()', '': totalPurchaseCost },
      { '': '   ()', '': totalSaleIncome },
      { '': ' ()', '': totalSaleIncome - totalPurchaseCost },
      { '': '   (/)', '': averagePurchasePrice },
      { '': '   (/)', '': averageSalePrice },
      { '': '', '': coefficient },
      { '': ' (%)', '': profitMargin }
    ];
    
    const summarySheet = XLSX.utils.json_to_sheet(summaryData);
    XLSX.utils.book_append_sheet(workbook, summarySheet, '');
    
    //   Excel
    const fileName = `fuel_report_${new Date().toISOString().slice(0, 10)}.xlsx`;
    XLSX.writeFile(workbook, fileName);
    
    return {
      success: true,
      fileName
    };
  } catch (error) {
    console.error('Export error:', error);
    return {
      success: false,
      error: error instanceof Error ? error.message : String(error)
    };
  }
}; 