//  
export type UserRole = 'admin' | 'moderator' | 'worker';

//  
export interface User {
  id: string;
  name: string;
  role: UserRole;
  username: string;
  password: string;
}

//    
export const rolePermissions = {
  admin: {
    canEdit: true,
    canDelete: true,
    canFreeze: true,
    canAddUsers: true,
    canViewReports: true,
    canExport: true,
    canManageOrders: true,
    canManageShifts: true,
  },
  moderator: {
    canEdit: true,
    canDelete: false,
    canFreeze: true,
    canAddUsers: false,
    canViewReports: true, 
    canExport: true,
    canManageOrders: true,
    canManageShifts: true,
  },
  worker: {
    canEdit: false,
    canDelete: false,
    canFreeze: false,
    canAddUsers: false,
    canViewReports: false,
    canExport: false,
    canManageOrders: false,
    canManageShifts: false,
  }
};

//  
export const initializeUsers = (): void => {
  const users = localStorage.getItem('users');
  
  if (!users) {
    const defaultUsers: User[] = [
      {
        id: 'admin1',
        name: '',
        role: 'admin',
        username: 'admin',
        password: 'admin123'
      },
      {
        id: 'moderator1',
        name: '',
        role: 'moderator',
        username: 'moderator',
        password: 'mod123'
      },
      {
        id: 'worker1',
        name: '',
        role: 'worker',
        username: 'worker',
        password: 'worker123'
      }
    ];
    
    localStorage.setItem('users', JSON.stringify(defaultUsers));
  }
  
  //   ,   
  if (!localStorage.getItem('currentUser')) {
    localStorage.setItem('currentUser', JSON.stringify({
      id: 'admin1',
      name: '',
      role: 'admin'
    }));
  }
};

//  
export const loginUser = (username: string, password: string): User | null => {
  const users = JSON.parse(localStorage.getItem('users') || '[]');
  const user = users.find((u: User) => u.username === username && u.password === password);
  
  if (user) {
    const { id, name, role } = user;
    localStorage.setItem('currentUser', JSON.stringify({ id, name, role }));
    return user;
  }
  
  return null;
};

//  
export const logoutUser = (): void => {
  localStorage.removeItem('currentUser');
};

//   
export const getCurrentUser = (): { id: string; name: string; role: UserRole } | null => {
  const userStr = localStorage.getItem('currentUser');
  return userStr ? JSON.parse(userStr) : null;
};

//   
export const checkPermission = (permission: keyof typeof rolePermissions.admin): boolean => {
  const user = getCurrentUser();
  if (!user) return false;
  
  return rolePermissions[user.role][permission] || false;
}; 